var Slider = require('./slider');
var Filter = require('./filter');
var tools = require('./tools');
var sliderDetail = require('./slider-detail');
var filterProcessor = require('./filter-processor');
var authenticator = require('./authenticator');
var salesforceRepo = require('./salesforce-repo');
var config = tools.getAppConfig();

var sliders = [];

// 2 second delay between loading individual slider's history on initial page load
// don't stagger on ie9 for timing reasons with the history polling
var webSocketClass = window['MozWebSocket'] ? window.MozWebSocket : window.WebSocket;
var DELAY_BETWEEN_SLIDER_HISTORY_CALLS = typeof webSocketClass !== 'undefined' ? 2000 : 2000;

function processAction(action) {
	for (var index=0;index<sliders.length;index++) {
		var currentSlider = sliders[index];
		if (actionMatchesFilter(action, currentSlider.filter)) {
			currentSlider.processAction(action);
		}
	}
}

function actionMatchesFilter(action, filter) {
	var ret = filterProcessor.process(action, filter.criteria);
	if(ret && !filter.assignedToAllUsers){
		//we must have a prospect to pass
		if(action.hasOwnProperty('prospect') && (action.prospect !== null) ){
			//we have a prospect
			//now verify that the prospect is assigned to the user identified in the filter
			ret = (action.prospect.userId+"") === (filter.pardotOwnerId+"");
		}
		else{
			ret = false;
		}
	}
	return ret;
}

function addStoredSliders() {

	// try to get filters from Salesforce
	salesforceRepo.getFilters(function(sfFilters) {

		var storedFilters;
		if (!sfFilters) {
			console.log('Restoring Local Filters...');
			storedFilters = getLocalFilters();
		} else {
			storedFilters = sfFilters;
		}

		// create a slider for each filter
		var sliderNum = storedFilters.length - 1;
		storedFilters.forEach(function(filter) {
			var filterObj = new Filter(filter);
			// decrementing sliderNum, so the last slider added, which will be at the top of the page
			// is the first slider to load, and the first slider added, at the bottom of the page
			// is the last to load
			addSlider(addSliderTemplate(filterObj), null, sliderNum--);
		});

	})
}

function getLocalFilters() {

	// see if we have any defined
	if (!localStorage['filters']) {
		localStorage['filters'] = [];
		return [];
	}

	// get filters
	return JSON.parse(localStorage['filters']);

}

function saveSliders() {
	var filters = [];
	// grab the filter from each slider
	for (var sliderIndex in sliders) {
		var slider = sliders[sliderIndex];
		filters.push(slider.filter);
	}
	// put in an array in local storage for restoration
	localStorage['filters'] = JSON.stringify(filters);
}

function addSliderTemplate(filter) {
	var slider = new Slider(filter);
	// send to beginning of array and sliders div
	sliders.unshift(slider);
	slider.render();
	$('#sliders-container').prepend(slider.element).offset();
	$('#sliders-headers').prepend(slider.headerElement).offset();
	slider.show();
	return slider;
}

function addSlider(slider, callback, sliderNum) {
	if (typeof sliderNum === 'undefined' || sliderNum < 0) {
		sliderNum = 0;
	}

	// initial history load
	setTimeout(function() {
		slider.loadHistory(callback);
	}, DELAY_BETWEEN_SLIDER_HISTORY_CALLS * sliderNum);

	// if browser doesn't support websockets, use history polling as well
	var webSocketClass = window['MozWebSocket'] ? window.MozWebSocket : window.WebSocket;
	if (typeof webSocketClass === 'undefined') {
		// start polling after initial history load
		slider.pollHistory(callback);
	}
}

function removeSlider(slider) {

	// delete slider from the page
	sliders.splice(sliders.indexOf(slider), 1);
	// save sliders
	saveSliders();
	// if we have no sliders left, restore salesforce defaults
	if (sliders.length == 0) {
		addStoredSliders();
	}

}

function createSlider() {
	var filter = new Filter({
		isNewFilter: true,
		criteria: 'prospect!=null',
		assignedToAllUsers: true
	});
	// get current user
	salesforceRepo.getActiveUser(function(user) {
		// show the slider detail
		sliderDetail.show(filter, user.canViewNotAssignedProspects, function () {
			// hide the modal
			sliderDetail.hide();
			var slider = addSliderTemplate(filter);
			slider.showCreatingSpinner();
			// save filter in pardot/salesforce
			salesforceRepo.createFilter(filter, slider, function() {
				// add and render the filter via a slider
				slider.hideCreatingSpinner();
				slider.replaceSlider();
				addSlider(slider);
				// save sliders
				saveSliders();
			});
		});
	});
}

function renderSignout() {
	var logoutIFrame = $('#logout');
	$('a.logout').click(function() {
		logoutIFrame.load(function() {
			logoutIFrame.remove();
			window.location = config.url;
		});
		document.getElementById('logout').src = '/secur/logout.jsp';
	});
}

/**
 * Display an update message if the app/container version
 * is different from the code version.
 */
function checkForUpdate() {
	// Get the versions from the container/app and for
	// the actual code (in the built configuration)
	var appVersion = tools.getAppVersionUnsplit();
	var codeVersion = tools.getCodeVersion();
	// Assume that if the versions don't match, user needs
	// to download updated webkit container app

	if (appVersion !== codeVersion) {
		var downloadMessage = $('#download-message');
		showWarningBanner(downloadMessage);
	}
}


/**
 * Display warning message if IE version is less than 11
 */
function checkIEVersion() {
	//Get browser version
	var clientVersion = tools.getIEVersion();

	if (clientVersion && clientVersion < 11) {
		var ieWarningMessage = $('#older-ie-message');
		showWarningBanner(ieWarningMessage);
	}
}

function showWarningBanner(warningElement) {
	warningElement.show();

	// Make room for message
	var slidersContainer = $('#sliders-container');
	var currentTop1 = parseInt(slidersContainer.css('top'));
	slidersContainer.css('top', (currentTop1 + warningElement.outerHeight()) + 'px');
	var slidersScreen = $('#sliders-screen');
	var currentTop2 = parseInt(slidersScreen.css('top'));
	slidersScreen.css('top', (currentTop2 + warningElement.outerHeight()) + 'px');
}

function render() {

	addStoredSliders();
	renderSignout();

	// Check for download update if we're in the webkit
	if (tools.isDesktopVersion()) {
		checkForUpdate();
	}

	// Check IE version and throw message if version is less than 11
	if (!tools.isDesktopVersion()) {
		checkIEVersion();
	}
	
	$('a.add-slider').on('click', function (event) {
		event.preventDefault();
		createSlider();
	});

	var indexElement = $('#index');
	var headerElement = indexElement.children('.header');

	// hide sign out button if not in desktop version
	if (!tools.isDesktopVersion()) {
		headerElement.find('.buttons > .logout').hide();
		headerElement.find('.buttons > .help-button').css('border-radius', '0 .25rem .25rem 0');
	}

	// Synchronize scrolling of sliders and column headers
	$('#sliders-container').on('scroll', function () {
		$('#sliders-headers').scrollLeft($(this).scrollLeft());
	});

	// show page
	indexElement.fadeIn('fast');

}

module.exports = {
	removeSlider: removeSlider,
	processAction: processAction,
	saveSliders: saveSliders,
	render: render
};



