var config = require('./config-built.js');
var salesforceRepo = require('./salesforce-repo');
var tools = require('./tools');
var pushApi;

function initialize(pushUrl, messageCallback) {
	createAndConnectPushApi(pushUrl, messageCallback);
};

function createAndConnectPushApi(pushUrl, messageCallback) {
	// depends on PushApi class to detect websocket compatibility
	// and to take a different approach/do nothing if incompatible
	pushApi = new PushApi({
		authenticationSuccess: authenticationSuccess,
		processMessage: messageCallback,
		disconnected: pushApiDisconnected,
		connected: pushApiConnected,
		authenticationError: authenticationError,
		url: pushUrl
	});
	// immediately connect to push (RTF), auth can come later
	pushApi.connect();
};

function pushApiConnected() {
	// push api online, authenticate
	authenticate();
};

function pushApiDisconnected() {
};

//function used to grab debug params
function getParameterByName(name) {
	name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
	var regex = new RegExp("[\\?&]" + name + "=([^&#]*)"),
			results = regex.exec(location.search);
	return results === null ? "" : decodeURIComponent(results[1].replace(/\+/g, " "));
}

function authenticate() {
	// get current authed user
	salesforceRepo.getActiveUser(function(user) {
		// send Pardot user and api keys to RTF
		var debugEmail = getParameterByName('debugRtfAccount');
		var debugPassword;

		if ((tools.isDev() || tools.isDebug()) && debugEmail) {
			debugPassword = window.prompt("Please provide your password for "+debugEmail,"");
			pushApi.authenticate({
				email: debugEmail,
				password: debugPassword
			});
		} else {
			pushApi.authenticate({
				userKey: user.pardotUserKey,
				apiKey: user.pardotApiKey
			});
		}
	});
};

function authenticationSuccess() {
};

function authenticationError(error) {
};

function authorizationError(error) {
};

module.exports = {
	initialize: initialize,
	authenticate: authenticate
};



