var tools = require('./tools');
var activeUser;

// Global remoting timeout (10s)
Visualforce.remoting.timeout = 10000;
// maximum number of times to retry remote call on timeout/fail
var MAX_TRIES = 3;
var RETRY_PAUSE = 500;

function createFilter(filter, slider, callback, tryCount){
	tryCount = typeof tryCount !== 'undefined' ? ++tryCount : 1;
	Visualforce.remoting.Manager.invokeAction(
		window.apex.remotes.createFilter,
		convertLeaddeckFilterToSalesforceFilter(filter),
		tryCount,
		function(sfFilter, event){
			// successful call
			if (event.status) {
				// set id & criteria hash
				filter.id = sfFilter.id;
				filter.criteriaHash = sfFilter.criteriaHash;
				filter.title = sfFilter.title;
				callback();
			// fail gracefully after retrying the max tries
			} else if (tryCount >= MAX_TRIES) {
				tools.showConnectionErrorMessage(event, 'createFilter');
				slider.hideCreatingSpinner();
			// otherwise try again
			} else {
				setTimeout(function() {
					createFilter(filter, slider, callback, tryCount)
				}, RETRY_PAUSE);
			}
		},
		{escape: false, buffer: false}
	);
}

