function convertLeaddeckFilterToSalesforceFilter(filter){
	return {
		title: filter.title,
		showNotifications: filter.showNotifications,
		displayMode: filter.displayMode,
		pardotOwnerId: filter.pardotOwnerId,
		criteriaHash: filter.criteriaHash,
		assignedToAllUsers: filter.assignedToAllUsers,
		criteria: JSON.stringify(filter.criteria),
		id: filter.id
	};
}

function convertSalesforceFilterToLeaddeckFilter(filter){
	return {
		title: filter.title,
		showNotifications: filter.showNotifications,
		displayMode: filter.displayMode,
		pardotOwnerId: filter.pardotOwnerId,
		criteriaHash: filter.criteriaHash,
		assignedToAllUsers: filter.assignedToAllUsers,
		criteria: JSON.parse(filter.criteria),
		id: filter.id
	};
}

