var openExternalLink = require('./open-external-link');
var showingNav = false;
var showingSearchBar = false;

function render() {
	$('#top-nav').show();
	showingNav = true;
}

function processAction(action) {
	if (!showingNav || showingSearchBar)
		return;

	var crmUrl = action.getCrmUrl();
	if (crmUrl) {
		setupSearchBar(crmUrl);
		showingSearchBar = true;
	}
}

function setupSearchBar(crmUrl) {
	var sfUrl = getBaseUrl(crmUrl);
	var searchBar = $('#top-nav .search-bar-wrap');

	onQueryEnteredInSearchBar(searchBar, function (query) {
		searchSalesForce(sfUrl, query);
	});

	searchBar.show(300);
}

function getBaseUrl(url) {
	return url.substring(0, url.indexOf('.com/') + 5);
}

function onQueryEnteredInSearchBar(searchBar, callback) {
	searchBar.on('keypress', 'input[type="text"]', function (e) {
		if (enterKeyWasPressed(e)) {
			var value = $(e.target).val();
			callback(value);
		}
	});
}

function searchSalesForce(sfUrl, query) {
	var searchUri = sfUrl + '_ui/search/ui/UnifiedSearchResults?str=' + encodeURIComponent(query);
	openExternalLink(searchUri);
}

function enterKeyWasPressed(event) {
	return (event.keyCode ? event.keyCode : event.which) === 13;
}

module.exports = {render: render, processAction: processAction};



