var tools = require('./tools');
var cardTemplate = require('jade!../templates/card.jade');
var cardNotificationTemplate = require('jade!../templates/card-notification.jade');

var Card = function (action, stack) {
	this.action = action;
	this.stack = stack;
};

Card.prototype.getTitle = function () {
	return this.action.getTitle(this.stack.slider.filter.displayMode);
};

Card.prototype.getSubtitle = function () {
	return this.action.getSubtitle(this.stack.slider.filter.displayMode);
};

Card.prototype.getTypeClass = function () {
	return this.action.getTypeClass ();
};

Card.prototype.refreshPrettyDate = function () {
	this.element.find('.date').text(this.action.getPrettyDate());
};

Card.prototype.getNotificationBody = function() {
	return cardNotificationTemplate(this);
}

Card.prototype.render = function () {
	// create card element
	this.element = $(cardTemplate(this));
	// add to stack's inner container (so that it's sinkable)
	this.stack.sinkableElement.append(this.element);
};

module.exports = Card;



