var messageTemplate = require('jade!../templates/message-notification.jade');
var tools = require('./tools');
var element;

function show(headMsg, bodyMsg, bodyItems, okayCB) {

	tools.setCookie('isfirstv','0');

	// render card detail inner element
	element = $(messageTemplate({
		head: headMsg,
		body: bodyMsg,
		bodyChanges: bodyItems
	}));

	// update card detail inner html
	var messageInfo = $('#newChangesNotification');
	messageInfo.html(element);

	// show modal
	var modalBackdrop = $('#modal-backdrop');
	modalBackdrop.addClass('slds-backdrop--open');
	element.addClass('slds-fade-in-open');

	// add a click handler to the confirm yes button
	element.find('.slds-modal__footer .slds-button.slds-button--brand').on('click', hide);

	// close buttons
	element.find('.slds-modal__header .slds-button.slds-modal__close').on('click', hide);
	$(document).on('keydown', function (e) {
		if (e.keyCode === 27) {
			hide();
			$(document).off('keydown');
		}
	});

}

function hide() {
	var modalBackdrop = $('#modal-backdrop');
	element.removeClass('slds-fade-in-open');
	modalBackdrop.removeClass('slds-backdrop--open');
};

module.exports.show = show;



