var sliderDetailTemplate = require('jade!../templates/slider-detail.jade');
var sliderDetailLocationTemplate = require('jade!../templates/slider-detail-location.jade');
var tools = require('./tools');

var element;
var filter;
var complete;

function show(for_filter, canViewNotAssignedProspects, on_complete) {

	// set current filter & render
	filter = for_filter;
	// set callback
	complete = on_complete;

	// render slider detail inner element
	element = $(sliderDetailTemplate({
		filter: filter,
		canViewNotAssignedProspects: canViewNotAssignedProspects
	}));

	// bind edit title & add locations
	element.on('change', '.country-group', function () {
			addLocation('country-group');
		})
		.on('change', '.country', function () {
			addLocation('country');
		})
		.on('change', '.state', function () {
			addLocation('state');
		})
		.on('click', 'div[class*="-locations"] li .kill', removeLocation);

	// hide some things if in SF tab
	if (!tools.isDesktopVersion()) {
		element.find('.desktop-notifications').hide();
	}

	// update slider detail inner html and show
	var sliderDetail = $('#slider-detail');
	sliderDetail.html(element);

	// show modal
	var modalBackdrop = $('#modal-backdrop');
	modalBackdrop.addClass('slds-backdrop--open');
	element.addClass('slds-fade-in-open');

	// close buttons
	element.find('.slds-modal__footer .slds-button.slds-button--neutral').on('click', hide);
	element.find('.slds-modal__header .slds-button.slds-modal__close').on('click', hide);
	$(document).on('keydown', function (e) {
		if (e.keyCode === 27) {
			hide();
			$(document).off('keydown');
		}
	});

	// save button
	element.find('.slds-modal__footer .slds-button.slds-button--brand').on('click', save);

	// populate from existing filter
	populate();

	// if it's a new filter, focus on the title when the modal is shown
	if (!filter.id) {
		$('.title').find('input').focus();
	}

};

function removeLocation(event) {
	$(event.target).closest('li').remove();
}

function hide() {
	var modalBackdrop = $('#modal-backdrop');
	element.removeClass('slds-fade-in-open');
	modalBackdrop.removeClass('slds-backdrop--open');
};

function populate() {

	// set title
	element.find('input[name=title]').val(filter.getTitle());
	// set show notifications
	element.find('input[name=showNotifications]').prop('checked', filter.showNotifications ? true : false);
	// if we are a new filter, select all prospects assigned to me
	if (!filter.id) {
		element.find('.radio[data-name=myLeadsAndContacts] input').prop('checked', true);
	}
	// make sure we have something
	if (!filter.criteria) {
		return;
	}

	if (filter.criteria.length == 0) {
		return;
	}

	// populate prospect criteria
	populateCriteria(filter.criteria, prospectCriterionPopulator);
	// populate activity criteria
	populateCriteria(filter.criteria, activityCriterionPopulator);
	// populate prospect criteria
	populateCriteria(filter.criteria, geoCriterionPopulator);

}

function populateCriteria(criteria, criterionPopulator) {

	// see if we can populate the whole criteria expression
	if (criterionPopulator(criteria)) {
		return;
	}

	// see what sort of optimizations have been done in filter save
	if (criteria.hasOwnProperty('a')) {
		// loop over all anded wells
		criteria.a.forEach(function(andCriteria) {
			populateCriteria(andCriteria, criterionPopulator);
		});
	} else if (criteria.hasOwnProperty('o')) {
		// loop over the ors for a single well
		criteria.o.forEach(function(orCriteria) {
			populateCriteria(orCriteria, criterionPopulator);
		});
	}

}

function prospectCriterionPopulator(criterion) {

	// get the JSON version of this criterion
	if (typeof criterion == 'string') {
		criterion = "'" + criterion + "'";
	} else {
		// convert criterion to string
		criterion = JSON.stringify(criterion);
		// replace all ' with "
		criterion = criterion.replace(/\"/g, "'");
	}

	var assignedToAllUsers = filter.assignedToAllUsers ? 'true' : 'false';
	// look for the data attribute
	var inputElement = element.find('.prospect-criteria input[data-criteria="' + criterion + '"]' + '[data-assignedToAllUsers=' + assignedToAllUsers + ']');
	if (inputElement.length == 0) {
		return false;
	}

	// check the radio
	inputElement.prop('checked', true);
	// we found the element
	return true;

}

function activityCriterionPopulator(criterion) {

	criterion = getCriterionString(criterion);
	// look for the data attribute
	var inputElement = element.find('.activity-criteria input[data-criteria="' + criterion + '"]');
	if (inputElement.length == 0) {
		return false;
	}

	// check the check
	inputElement.prop('checked', true);
	// we found the element
	return true;

}

function geoCriterionPopulator(criterion) {

	criterion = getCriterionString(criterion);
	// test for geo criteria
	var geoCriteriaRegex = /^{'o':\['prospect\.(countryGroup|country|state)(?:==|!=|<=|>=|>>|<<)(.*?)','whois\.(?:countryGroup|country|state)(?:==|!=|<=|>=|>>|<<)(?:.*?)'\]}$/g;
	var geoCriterionMatches = geoCriteriaRegex.exec(criterion);
	// verify we have three (name, comp, value)
	if (!geoCriterionMatches) {
		return false;
	}

	// extract name and location, add to pill box
	var name = geoCriterionMatches[1];
	var location = geoCriterionMatches[2];
	addLocationToPillbox(name, location);
	return true;

}

function getCriterionString(criterion) {
	// get the JSON version of this criterion
	if (typeof criterion == 'string') {
		return "'" + criterion + "'";
	} else {
		// convert criterion to string
		criterion = JSON.stringify(criterion);
		// replace all " with '
		return criterion.replace(/\"/g, "'");
	}
}

function addLocation(className) {

	// get the select element for this class
	var selectElement = element.find('select.' + className);
	// ensure we aren't on the first (default) option
	if (selectElement.find("option:selected").hasClass('ignored')) {
		return;
	}

	// get select element data name
	var name = selectElement.data('name');
	// now get the location
	var location = selectElement.val();
	addLocationToPillbox(name, location);

};

function addLocationToPillbox(name, location) {

	// see if pillbox already contains us
	var pills = element.find('.pillbox .pill-group li.pill[data-name="' + name + '"][data-value="' + location + '"]');
	if (pills.length != 0) {
		return false;
	}

	// render location element
	var locationElement = $(sliderDetailLocationTemplate({
		name: name,
		value: location
	}));
	// place in pillbox
	var locationsPillboxElement = element.find('.' + name + '-locations .pillbox ul');
	locationsPillboxElement.append(locationElement);

}

function save() {
	// set title
	filter.title = element.find('input[name=title]').val();
	// set show notifications
	filter.showNotifications = element.find('input[name=showNotifications]:checked').length > 0;

	// set assigned to all users
	var assignedToAllUsersElements = element.find('input[data-assignedToAllUsers=true]:visible:checked');
	filter.assignedToAllUsers = assignedToAllUsersElements.length > 0 ? true : false;

	// set display mode
	var companyDisplayModeElements = element.find('input[name=identifiedCompanies]:checked');
	filter.displayMode = companyDisplayModeElements.length > 0 ? 'company' : 'person';

	var andCriteria = [];
	// get or criteria for each well
	var prospectOrCriteria = getOrCriteria('.prospect-criteria.well input:radio:checked');
	var activityOrCriteria = getOrCriteria('.activity-criteria.well input:checkbox:checked');
	var geographicOrCriteria = getOrCriteria('.geo-criteria.well div[class*="-locations"] li');
	// add each criteria that has active sub-criteria
	if (prospectOrCriteria) {
		andCriteria.push(prospectOrCriteria)
	}
	if (activityOrCriteria) {
		andCriteria.push(activityOrCriteria)
	}
	if (geographicOrCriteria) {
		andCriteria.push(geographicOrCriteria)
	}

	var criteria;
	// set filter criteria
	if (andCriteria.length == 1) {
		criteria = andCriteria[0]
	} else if (andCriteria.length > 1) {
		criteria = "{'a':[" + andCriteria.join(',') + "]}";
	} else {
		criteria = null;
	}

	// replace all ' with "
	if (criteria) {
		criteria = criteria.replace(/\'/g, '"');
	}

	// set filter criteria
	try{
		filter.criteria = JSON.parse(criteria);
	}
	catch(e){
		console.log('Encountered error while parsing filter criteria created by the form:');
		console.log(e);
	}
	// run callback
	complete();

};

function getOrCriteria(criteriaElement) {

	// get checked or criteria for well
	var orCriteria = element.find(criteriaElement).map(function () {
		return $(this).data('criteria');
	}).get();
	// if we have no ors, return no and
	if (orCriteria.length == 0) {
		return null;
	}
	// just have one, return it straight up
	if (orCriteria.length == 1) {
		return orCriteria[0];
	}
	// put commas between each or for the overall and
	return "{'o':[" + orCriteria.join(',') + "]}";

};

module.exports = {
	show: show,
	hide: hide
};



