var errorTemplate = require('jade!../templates/error.jade');

function show(message) {
	var error = $('#error-message');
	// only show if it's not already shown
	if (error.css('display') !== 'none' ) {
		return;
	}

	// render inner element
	var element = $(errorTemplate({
		message: message
	}));

	// update inner html
	error.html(element);
	error.show();

	// make room for message
	var slidersContainer = $('#sliders-container');
	var currentTop1 = parseInt(slidersContainer.css('top'));
	slidersContainer.css('top', (currentTop1 + error.outerHeight()) + 'px');
	var slidersScreen = $('#sliders-screen');
	var currentTop2 = parseInt(slidersScreen.css('top'));
	slidersScreen.css('top', (currentTop2 + error.outerHeight()) + 'px');
}

module.exports.show = show;


