function getPrettyDate_OLD(date) {
	if (date) {
		date = moment(normalizeDateFormat(date), 'YYYY-MM-DD HH:mm:ss Z');
	} else {
		date = moment();
	}
	return date.fromNow();
}

function getUnixDate(date) {
	if (date) {
		date = moment(normalizeDateFormat(date), 'YYYY-MM-DD HH:mm:ss Z');
	} else {
		date = moment();
	}

	return date.unix();
}

function normalizeDateFormat(date) {
	date = date.toUpperCase();
	// Check if activity time occurred during DST (specify America/New_York timezone to observe DST)
	var isDST = moment(date).tz('America/New_York').isDST();
	var offset = isDST ? SERVER_TIMEZONE_OFFSET_DLS : SERVER_TIMEZONE_OFFSET;
	if ((date.indexOf('EST') < 0)) {
		//convert EST to be -0500 or -0400 since its coming in EST
		date = date + ' ' + offset;
	}
	else {
		date = date.replace(/EST/g, offset);
	}
	return date;
}

