var tagBody = '(?:[^"\'>]|"[^"]*"|\'[^\']*\')*';
var tagOrComment = new RegExp(
	'<(?:' +
	// Comment body.
	'!--(?:(?:-*[^->])*--+|-?)' +
	// Special "raw text" elements whose content should be elided.
	'|script\\b' + tagBody + '>[\\s\\S]*?</script\\s*' +
	'|style\\b' + tagBody + '>[\\s\\S]*?</style\\s*' +
	// Regular name
	'|/?[a-z]' +
	tagBody +
	')>',
	'gi');

function removeTags(html) {
	var oldHtml;
	do {
		oldHtml = html;
		html = html.replace(tagOrComment, '');
	}
	while (html !== oldHtml);
	return html;
}

function clean(input) {
	if (!input || 0 === input.length) {
		return null;
	}
	return removeTags(input + '');
}

function getUrlParameter(name, decode) {
	if (!window.location.search) {
		return null;
	}

	// split up params into key val pairs
	var parameters = window.location.search.split("?")[1].split("&");
	// loop through get params looking for passed
	for (var i = 0; i < parameters.length; i++) {
		// split into key and value
		var parameter = parameters[i].split("=");
		// see if we match a param
		if (parameter[0] == name) {
			return (decode) ? decodeURIComponent(parameter[1]) : parameter[1];
		}
	}

	// fail
	return false;

}

function getCookie(cname) {
	return new Promise(function(resolve, reject){
		if (gui) {
			getDesktopCookies().then(function(cookies) {
				resolve(cookies[cname])
			});
		} else {
			var value;
			var name = cname + "=";
			var ca = document.cookie.split(';');
			for (var i = 0; i < ca.length; i++) {
				var c = ca[i].trim();
				if (c.indexOf(name) === 0) {
					value = c.substring(name.length, c.length);
				}
			}
			resolve(value);
		}
	});
}

function deleteCookie(cname) {
	document.cookie = cname + "=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/";
}

/**
 * This method gets all the cookies in window.require("nw.gui")..App.dataPath
 */
function getDesktopCookies() {
	return new Promise(function(resolve, reject) {
		fs.readFile(cookiesFile, function(err, data) {
			if (err) {
				resolve({});
			} else {
				resolve(JSON.parse(data));
			}
		})
	})
}

function saveDesktopCookies(settings) {
	return new Promise(function(resolve, reject) {
		fs.open(cookiesFile, 'w+', function(err, fd) {
			if (err) {
				reject(err);
			} else {
				fs.writeFile(cookiesFile, settings, function(err) {
					if (err) {
						reject(err);
					} else {
						resolve(true);
					}
				})
			}
		})
	})
}

function setCookie(cname, cvalue) {
	if (isDesktopVersion()) {
		getDesktopCookies().then(function(cookies) {
			cookies[cname] = cvalue;
			saveDesktopCookies(JSON.stringify(cookies)).catch(console.log);
		});
	} else {
		document.cookie = cname + "=" + cvalue;
	}
}

/**
 * This method returns IE version only if the cleint is IE
 * Returns version number for
 */
function getIEVersion() {
	var ua = window.navigator.userAgent;

	// for IE 8, 9 and 10
	// ua = 'Mozilla/5.0 (compatible; MSIE 10.0; Windows NT 6.2; Trident/6.0)';
	var msie = ua.indexOf('MSIE ');
	if (msie > 0) {
		// IE 10 or older => return version number
		return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
	}

	// for IE 11
	// ua = 'Mozilla/5.0 (Windows NT 6.3; Trident/7.0; rv:11.0) like Gecko';
	var trident = ua.indexOf('Trident/');
	if (trident > 0) {
		// IE 11 => return version number
		var rv = ua.indexOf('rv:');
		return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
	}

	// for IE Edge
	// ua = 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/46.0.2486.0 Safari/537.36 Edge/13.10586';
	var edge = ua.indexOf('Edge/');
	if (edge > 0) {
		// Edge (IE 12+) => return version number
		return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
	}

	// other browser
	return false;
}

function setupNewWindowPolicy() {
	if (!gui) {
		return;
	}

	var win = gui.Window.get();
	win.on('new-win-policy', function (frame, url, policy) {
		//gui.Shell.openExternal(url);
		policy.forceNewPopup();
	});
}

function fixDockClicking() {

	if (!gui) {
		return;
	}

	var hidden = false;
	var win = gui.Window.get();
	// Listen to the minimize event
	win.on('minimize', function() {
		hidden = true;
	});

	// Listen to the focus event
	win.on('focus', function() {
		hidden = false;
	});

	// Listen to the blur event
	win.on('blur', function() {
		hidden = true;
	});

	gui.App.on('reopen', function() {
		if (hidden) {
			win.restore();
		} else {
			win.minimize();
		}
	});

}

function fixMacWindowMenu() {

	if (!gui) {
		return;
	}

	if (os.platform() != 'darwin') {
		return;
	}

	var win = gui.Window.get();
	var appTitle = gui.App.manifest.title;
	var menu = new gui.Menu({ type: "menubar" });
	menu.createMacBuiltin(appTitle, {
		hideEdit: true,
		hideWindow: true
	});
	win.menu = menu;

}

function handleWinClose() {

	if (!gui) {
		return;
	}

	var win = gui.Window.get();
	win.on('close', function() {
		gui.App.closeAllWindows();
		gui.App.quit();
	});

};

function resetWindowTitle() {

	if (!gui) {
		return;
	}

	var win = gui.Window.get();
	win.title = gui.App.manifest.window.title;

}

function getRandomInt(min, max) {
	return Math.floor(Math.random() * (max - min + 1)) + min;
}

function showConnectionErrorMessage(event, type) {
	var message;
	var filterOperationFailMessage;

	if (gui) {
		message = "We're having trouble connecting. Please check your connection and try closing and reopening Engage Alerts.";
	} else {
		message = "We're having trouble connecting. Please check your connection and try refreshing the page.";
	}

	if (event && event.message && event.message.indexOf('Logged in?') != -1) {
		if (gui) {
			message = "We're having trouble connecting. Your session may have timed out. Please try closing and reopening Engage Alerts.";
		} else {
			message = "We're having trouble connecting to Salesforce. Your session may have timed out. Please ensure you are signed in to Salesforce and refresh.";
		}
	}

	type = typeof type !== 'undefined' ? type : '';

	if (gui) {
		filterOperationFailMessage = "Please check your connection and try closing and reopening Engage Alerts.";
	} else {
		filterOperationFailMessage = "Please check your connection and try refreshing the page.";
	}

	// special handling of error types
	if (type === 'deleteFilter') {
		message = "We're having trouble deleting the column. " + filterOperationFailMessage;
	} else if (type === 'createFilter') {
		message = "We're having trouble creating the column. " + filterOperationFailMessage;
	} else if (type === 'updateFilter') {
		message = "We're having trouble updating the column. " + filterOperationFailMessage;
	}

	error.show(" " + message);
}

