var page = require('./page');
var Action = require('./action');
var openExternalLink = require('./open-external-link');
var topNav = require('./top-nav');
var authenticator = require('./authenticator');
var tools = require('./tools');
var message = require('./message-notification');

// get current config from app manifest
var config = tools.getAppConfig();

$(function () {
	initialize();
});

function initialize() {
	changesNotification();
	ensureHttps();
	setupExternalLinkClickHandlers();
	fallbackTojQueryAnimate();
	turnOnTopNav();
	tools.fixMacWindowMenu();
	tools.handleWinClose();
	tools.resetWindowTitle();
	render();
	authenticate();
}

function fallbackTojQueryAnimate() {
	// Delegate .transition() calls to .animate()
	// if the browser can't do CSS transitions.
	if (!$.support.transition) {
		$.fn.transition = $.fn.animate;
	}
}

function render() {
	page.render();
}

function authenticate() {
	// begin authentication
	authenticator.initialize(
		config.pushUrl,
		processMessage);
}

function ensureHttps() {
	if (location.hostname.indexOf('localhost') == -1) {
		if (location.protocol === 'http:') {
			location.protocol = 'https:';
		}
	}
}

/**
 * method to show notification of new changes
 */
function changesNotification() {
	var headMsg = 'Welcome to the new Engage Alerts';
	var bodyMsg = 'We\'ve made some updates to the Engage Alerts experience to ensure that your latest alerts are always visible.';
	var bodyItems = ['Sliders are now called columns.','Engagement activity cards are now displayed vertically and the latest alerts will appear at the top of each column.'];
	tools.getCookie('isfirstv').then(function(value){
		if (value != 0) {
			message.show(headMsg, bodyMsg, bodyItems);
		}
	})
}

function setupExternalLinkClickHandlers() {
	tools.setupNewWindowPolicy();
	$('body').on('click', 'a[target="_blank"]', function (e) {
		e.preventDefault();
		openExternalLink($(this).attr('href'));
	});
};

function processMessage(message) {

	if (!message.hasOwnProperty('action')) {
		return;
	}

	// convert to action, validate, process
	var action = new Action(message.action);
	var missing = action.validate();
	if (missing.length > 0) {
		missing.forEach(function(field) {
			console.log('Missing: ' + field);
		});
	} else {
		page.processAction(action);
		topNav.processAction(action);
	}

};

function turnOnTopNav() {
	if (tools.getUrlParameter('topnav')) {
		topNav.render();
	}
};



