var tools = require('./tools');

var Action = function (action) {

	// merge in all action properties
	for (var propertyName in action) {
		this[propertyName] = action[propertyName];
	}

};

/**
 * Returns normalized action type for displaying to a user.
 * For instance, form submits on a landing page have a type of Landing Page Success,
 * but will get returned as Form Success
 */
Action.prototype.getTypeForDisplay = function () {
	var typeForDisplay = this.type;
	switch (this.type) {
		case 'Form Success':
		case 'Form Handler Success':
		case 'Landing Page Success':
			typeForDisplay = 'Form Success';
			break;
		case 'Form Error':
		case 'Form Handler Error':
		case 'Landing Page Error':
			typeForDisplay = 'Form Error';
			break;
		case 'Site Search Query':
			typeForDisplay = 'Site Search';
			break;
		case 'Social Post Click':
			typeForDisplay = 'Social Post Click';
			break;
		case 'Multivariate Landing Page':
			typeForDisplay = 'Landing Page';
			break;
		case 'Email Spam Complaint':
			typeForDisplay = 'Spam Complaint';
			break;
		case 'Email Preferences Open':
			typeForDisplay = 'Preferences';
			break;
		case 'Email Unsubscribe':
			typeForDisplay = 'Unsubscribe';
			break;
	}

	return typeForDisplay;
};

Action.prototype.getTitle = function (displayMode) {
	if (displayMode == 'person' && !this.getPerson()) {
		displayMode = 'company';
	}

	if (displayMode == 'person') {
		var title = this.getPerson();
		// check for null
		if (title) {
			return title;
		}
		return 'Anonymous Visitor';
	} else if (displayMode == 'company') {
		var title = this.getCompany();
		// check for null
		if (title) {
			return title;
		}
		return 'Unknown Company';
	}
	return 'Unknown';
};

Action.prototype.getTitleText = function() {
	var title = this.getJobTitle();
	if (title) {
		return title + ' at ';
	} else {
		return '';
	}
};

Action.prototype.getSubtitle = function (displayMode) {
	if (displayMode == 'person' && !this.getPerson()) {
		displayMode = 'company';
	}

	if (displayMode == 'person') {
		var subtitle = this.getCompany();
		// check for null
		if (subtitle) {
			return subtitle;
		}
	} else if (displayMode == 'company') {
		var location = this.getLocation();

		if (location) {
			return location;
		}
	}
	return 'Unknown';
};

Action.prototype.getPerson = function () {
	if (this.prospect){
		if (this.prospect.firstName && this.prospect.lastName) {
			return tools.clean(this.prospect.firstName + ' ' + this.prospect.lastName);
		} else if (this.prospect.firstName){
			return tools.clean(this.prospect.firstName);
		} else if (this.prospect.lastName){
			return tools.clean(this.prospect.lastName);
		} else if(this.prospect.email){
			return this.prospect.email;
		} else {
			return 'Name Unknown';
		}
	}
	return null;
};

Action.prototype.getCompany = function () {
	return tools.clean(this.getMostSpecificPropertyValue('company'));
};

Action.prototype.getLocation = function () {
	var city = this.getCity();
	var stateAbbreviation = this.getStateAbbreviation();
	var country = this.getCountry();
	if (city && stateAbbreviation && country) {
		return city + ', ' + stateAbbreviation + ' ' + country;
	} else if (stateAbbreviation && country) {
		return stateAbbreviation + ', ' + country;
	} else if (country) {
		return country;
	}

	return null;
};

Action.prototype.getAddressOne = function () {
	if (this.prospect && this.prospect.addressOne && (this.prospect.addressOne != '')) {
		return tools.clean(this.prospect.addressOne);
	}
	return null;
};

Action.prototype.getJobTitle = function () {
	if (this.prospect && this.prospect.jobTitle && (this.prospect.jobTitle != '')) {
		return tools.clean(this.prospect.jobTitle);
	}
	return null;
};

Action.prototype.getAddressTwo = function () {
	if (this.prospect && this.prospect.addressTwo && (this.prospect.addressTwo != '')) {
		return tools.clean(this.prospect.addressTwo);
	}
	return null;
};

Action.prototype.getCity = function () {
	return this.getMostSpecificPropertyValue('city');
};

Action.prototype.getState = function () {
	return this.getMostSpecificPropertyValue('state');
};

Action.prototype.getStateAbbreviation = function () {
	return this.getMostSpecificPropertyValue('stateAbbreviation');
};

Action.prototype.getZip = function () {
	return this.getMostSpecificPropertyValue('zip');
};

Action.prototype.getCountry = function () {
	return this.getMostSpecificPropertyValue('country');
};

Action.prototype.getEmail = function () {
	return this.getMostSpecificPropertyValue('email');
};

Action.prototype.getHostname = function () {
	if (this.whois && this.whois.hostname && (this.whois.hostname != '')) {
		return tools.clean(this.whois.hostname);
	}
	return null;
};

Action.prototype.getIpAddress = function () {
	if (this.whois && this.whois.ipAddress && (this.whois.ipAddress != '')) {
		return tools.clean(this.whois.ipAddress);
	}
	return null;
};

Action.prototype.getTypeClass = function () {
	return this.type.replace(/ /g, '-').toLowerCase();
}

Action.prototype.getPrettyDate = function () {
	return tools.getPrettyDate(this.createdAt);
};

Action.prototype.getUnixDate = function () {
	return tools.getUnixDate(this.createdAt);
};

Action.prototype.getCrmUrl = function () {
	if (typeof this.prospect !== 'undefined' &&
		this.prospect.crmUrl &&
		this.prospect.crmUrl != '') {
		return tools.clean(this.prospect.crmUrl);
	}
	return null;
};

Action.prototype.getRecordId = function () {
	if (typeof this.prospect !== 'undefined') {
		return this.prospect.crmLeadFid || this.prospect.crmContactFid || this.prospect.email;
	}
	return null;
};

Action.prototype.getPhoneHref = function () {
	if (window.navigator.userAgent.indexOf("MSIE ") > 0) {   // desktop IE errors on hrefs with tel: 
		return '';
	} else {
		return 'tel:' + this.prospect.phone.replace(/[^\d\+]/g, '');
	} 
}

Action.prototype.isStarred = function () {
	return this.getMostSpecificPropertyValue('isStarred');
}

Action.prototype.getMostSpecificPropertyValue = function (property) {
	if (this.prospect && this.prospect[property] && (this.prospect[property] != '')) {
		return tools.clean(this.prospect[property]);
	} else if (this.whois && (this.whois[property] != '')) {
		return tools.clean(this.whois[property]);
	} else {
		return null;
	}
};

Action.prototype.getLinkedInUrl = function () {

	var searchType;
	if (this.getPerson()) {
		searchType = 'people';
	} else if (this.getCompany()) {
		searchType = 'company';
	} else {
		return null;
	}

	// return formatted url
	return 'http://www.linkedin.com/search/fpsearch?type=' + searchType + '&keywords=' + this.getEncodedSearchKeywords();

};

Action.prototype.getIsSearchable = function () {
	return this.getPerson() || this.getCompany();
};

Action.prototype.getGoogleUrl = function () {
	return 'https://www.google.com/search?q=' + this.getEncodedSearchKeywords();
};

Action.prototype.getEncodedSearchKeywords = function () {

	var searchKeywords;
	var person = this.getPerson();
	var company = this.getCompany();
	if (person) {
		if (company) {
			searchKeywords = person + ' ' + company;
		} else {
			searchKeywords = person;
		}
	} else if (company) {
		searchKeywords = company;
	} else {
		searchKeywords = null;
	}

	// encode search keywords
	if (searchKeywords) {
		return encodeURIComponent(searchKeywords);
	} else {
		return null;
	}

};

Action.prototype.getPardotUrl = function () {
	if (this.prospect) {
		return "https://pi.pardot.com/prospect/read/id/" + this.prospect.id;
	} else if (this.visitor) {
		return "https://pi.pardot.com/visits/index/visitor_id/" + this.visitor.id;
	} else {
		return null;
	}
};

Action.prototype.getDetails = function () {

	var details;
	switch (this.type) {

		case 'Email':
		case 'Email Open':
		case 'Email Click':
		case 'Email Unsubscribe':
		case 'Email Bounce':
		case 'Email Spam Complaint':
		case 'Email Preferences Open':
			details = (this.email)? this.email.subject: '';
			break;

		case 'Form':
		case 'Form Success':
		case 'Form Error':
			details = (this.form)? this.form.name: '';
			break;

		case 'Form Handler':
		case 'Form Handler Success':
		case 'Form Handler Error':
			details = (this.formHandler)? this.formHandler.name: '';
			break;

		case 'File':
			details = (this.file)? this.file.name: '';
			break;

		case 'Landing Page':
		case 'Landing Page Success':
		case 'Landing Page Error':
		case 'Multivariate Landing Page':
		case 'Multivariate Landing Page Success':
		case 'Multivariate Landing Page Error':
			details = (this.landingPage)? this.landingPage.name: '';
			break;

		case 'Site Search Query':
			details = (this.siteSearchQuery)? this.siteSearchQuery.query: '';
			break;

		case 'Paid Search Ad':
			details = (this.paidSearchAd)? this.paidSearchAd.headline: '';
			break;

		case 'Page View':
			details = (this.pageView)? this.pageView.title: '';
			break;

		case 'Video':
			details = (this.video)? this.video.name: '';
			break;

		case 'Custom Redirect':
			details = (this.customUrl)? this.customUrl.name: '';
			break;

		default:
			details = '';
			break;

	}

	// never return nothing
	if (!details || (details == '')) {
		details = 'Untitled';
	}

	// clean out html stuff
	return tools.clean(details);

};

Action.prototype.validate = function () {

	var missing = [];
	var that = this;
	function checkMissing(field) {
		var dots = field.split('.');
		var obj = that;
		while (dots.length && (obj = obj[dots.shift()]));
		// verify we used all our dots
		if (dots.length != 0) {
			missing.push(field);
			return true;
		}
		// verify the obj is not undefined
		if (obj === undefined) {
			missing.push(field);
			return true;
		}
		// found
		return false;
	}

	checkMissing('id');
	checkMissing('accountId');
	checkMissing('uuid');
	checkMissing('createdAt');

	if (this.hasOwnProperty('campaign')) {
		checkMissing('campaign.id');
		checkMissing('campaign.name');
		checkMissing('campaign.cost');
	}

	if (this.hasOwnProperty('prospect')) {
		checkMissing('prospect.id');
		checkMissing('prospect.salutation');
		checkMissing('prospect.firstName');
		checkMissing('prospect.lastName');
		checkMissing('prospect.email');
		checkMissing('prospect.company');
		checkMissing('prospect.website');
		checkMissing('prospect.jobTitle');
		checkMissing('prospect.department');
		checkMissing('prospect.country');
		checkMissing('prospect.countryAbbreviation');
		checkMissing('prospect.addressOne');
		checkMissing('prospect.addressTwo');
		checkMissing('prospect.city');
		checkMissing('prospect.state');
		checkMissing('prospect.stateAbbreviation');
		checkMissing('prospect.territory');
		checkMissing('prospect.zip');
		checkMissing('prospect.phone');
		checkMissing('prospect.fax');
		checkMissing('prospect.source');
		checkMissing('prospect.annualRevenue');
		checkMissing('prospect.employees');
		checkMissing('prospect.industry');
		checkMissing('prospect.yearsInBusiness');
		checkMissing('prospect.comments');
		checkMissing('prospect.score');
		checkMissing('prospect.grade');
		checkMissing('prospect.isDoNotCall');
		checkMissing('prospect.isDoNotEmail');
		checkMissing('prospect.isOptedOut');
		checkMissing('prospect.isReviewed');
		checkMissing('prospect.isStarred');
		checkMissing('prospect.crmLeadFid');
		checkMissing('prospect.crmContactFid');
		checkMissing('prospect.crmOwnerFid');
		checkMissing('prospect.crmAccountFid');
		checkMissing('prospect.crmUrl');
		checkMissing('prospect.userId');
	}

	if (this.hasOwnProperty('opportunity')) {
		checkMissing('opportunity.id');
		checkMissing('opportunity.value');
		checkMissing('opportunity.probability');
		checkMissing('opportunity.stage');
		checkMissing('opportunity.type');
		checkMissing('opportunity.isWon');
		checkMissing('opportunity.isClosed');
		checkMissing('opportunity.isArchived');
	}

	if (this.type.indexOf('Email') > -1) {
		checkMissing('email.id');
		checkMissing('email.name');
		checkMissing('email.isOneToOne');
		checkMissing('email.subject');
	}

	if ((this.type == 'Form Success') || (this.type == 'Form Error')) {
		checkMissing('form.name');
	}

	if ((this.type == 'Form Handler Success') || (this.type == 'Form Handler Error')) {
		checkMissing('formHandler.name');
	}

	if (this.type == 'File') {
		checkMissing('file.name');
	}

	if (this.type == 'Landing Page') {
		checkMissing('landingPage.name');
	}

	if (this.type == 'Visitor') {
		checkMissing('visitor.name');
	}

	if (this.hasOwnProperty('whois')) {
		checkMissing('whois.company');
		checkMissing('whois.city');
		checkMissing('whois.state');
		checkMissing('whois.stateAbbreviation');
		checkMissing('whois.zip');
		checkMissing('whois.country');
		checkMissing('whois.countryAbbreviation');
		checkMissing('whois.ipAddress');
		checkMissing('whois.email');
	}

	if (this.type == 'Site Search Query') {
		checkMissing('siteSearchQuery.query');
	}

	if (this.type == 'Paid Search Ad') {
		checkMissing('paidSearchAd.headline');
	}

	if (this.type == 'Custom Url') {
		checkMissing('customUrl.headline');
	}

	if (this.type == 'Social Message Link') {
		checkMissing('socialMessageLink.name');
		checkMissing('socialMessageLink.url');
	}

	if (this.type == 'Video') {
		checkMissing('video.name');
		checkMissing('video.url');
	}

	if (this.type == 'Webinar') {
		checkMissing('webinar.name');
	}

	if (this.type == 'Web Visit') {
		checkMissing('pageView.title');
		checkMissing('pageView.url');
	}

	return missing;

};

module.exports = Action;



